@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gruppo&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #F4F4F4;
  color: #37474F;
  overflow-y: scroll;
}

main {
  max-width: 1024px;
  margin: 0 auto;
}

.result.pt-border {
  border: 2px solid #20abfe;
}
.result .pt-text {
  color: #20abfe;
}
.result.rs-border {
  border: 2px solid #6bc424;
}
.result .rs-text {
  color: #6bc424;
}
.result.wew-border {
  border: 2px solid #fe7320;
}
.result .wew-text {
  color: #fe7320;
}

.unactive {
  opacity: 0.3;
  filter: grayscale(100%);
}

.checkbox-container {
  margin: 10px;
  display: flex;
  align-items: center;
}
.checkbox-container .checkbox {
  display: block;
  position: relative;
  height: 20px;
  width: 20px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Hide the browser's default checkbox */
  /* Create a custom checkbox */
  /* On mouse-over, add a background color */
  /* When the checkbox is checked, add a background */
  /* When the checkbox is checked, add a background */
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Show the checkmark when checked */
  /* Style the checkmark/indicator */
}
.checkbox-container .checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
}
.checkbox-container .checkbox .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  border: 1px #fe7320 solid;
  border-radius: 3px;
}
.checkbox-container .checkbox:hover input ~ .checkmark {
  background-color: rgba(254, 115, 32, 0.2196078431);
}
.checkbox-container .checkbox input:checked ~ .checkmark {
  background-color: #fe7320;
  border: 1px #fe7320 solid;
}
.checkbox-container .checkbox input:checked:hover ~ .checkmark {
  background-color: #1962b3;
  border: 1px #1962b3 solid;
}
.checkbox-container .checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.checkbox-container .checkbox input:checked ~ .checkmark:after {
  display: block;
}
.checkbox-container .checkbox .checkmark:after {
  left: 5px;
  top: 2px;
  width: 5px;
  height: 9px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(35deg);
  -ms-transform: rotate(35deg);
  transform: rotate(35deg);
}
.checkbox-container .label-text {
  display: inline-block;
  font-size: 14px;
  cursor: pointer;
  margin: 0 7px;
}

.filters {
  margin-top: 30px;
}
.filters .search {
  display: flex;
  justify-content: right;
  align-items: center;
}
.filters .search .search-input {
  display: flex;
  width: 100%;
}
.filters .search .search-input input {
  background-color: white;
  width: 100%;
  height: 100%;
  padding: 10px 5px;
  border-radius: 5px;
  border: 1px solid #fe7320;
}
.filters .search .search-btn {
  padding: 10px 20px;
  color: white;
  background-color: #fe7320;
  border-radius: 5px;
  border: 2px solid #fe7320;
  cursor: pointer;
  margin: 0 20px;
}
.filters .search .search-btn:hover {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2), -2px -2px 4px rgba(0, 0, 0, 0.2);
}
.filters .search .search-btn:active {
  scale: 0.95;
}
.filters .info-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  margin: 10px 0 20px 0;
  height: 23px;
}
.filters .info-bar .clear-filters {
  font-size: 11px;
  color: #37474F;
  opacity: 0.5;
  cursor: pointer;
}
.filters .info-bar .clear-filters:hover {
  color: #fe7320;
  opacity: 1;
}
.filters .drop-down-filters {
  max-height: 0;
  opacity: 0;
  transition: all 1s;
}
.filters .drop-down-filters.show {
  opacity: 1;
}
.filters .drop-down-filters .salary {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.filters .drop-down-filters .loc-and-checkboxes {
  display: flex;
  margin-bottom: 10px;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container {
  display: flex;
  flex-grow: 1;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .checkbox-container {
  display: flex;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  justify-content: center;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list label {
  margin-bottom: 5px;
  width: 100%;
  text-align: center;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list select {
  background-color: white;
  color: #37474F;
  padding: 5px 0;
  border-radius: 3px;
  border: 1px solid #fe7320;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list select option.placeholder {
  color: #D3D3D3;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list select:disabled {
  border: 1px solid #D3D3D3;
  color: #D3D3D3;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list select.cities option {
  color: #c5c3c3;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .list select.cities option.active {
  color: #37474F;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .distance-slider {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 20px;
}
.filters .drop-down-filters .loc-and-checkboxes .loc-container .distance-slider input {
  width: 100%;
}
.filters .drop-down-filters .loc-and-checkboxes .checkboxes-container {
  display: flex;
}
.filters .drop-down-filters .lists {
  display: flex;
  justify-content: space-around;
}
.filters .drop-down-filters .lists .list {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.filters .drop-down-filters .lists .list label {
  margin-bottom: 5px;
  border-bottom: 2px solid #fe7320;
  width: 100%;
  text-align: center;
  padding: 0 25px;
}
.filters .drop-down-filters .lists .list img {
  height: 15px;
  position: absolute;
  right: 5px;
  top: 4px;
  opacity: 0.4;
}
.filters .drop-down-filters .lists .list img:hover {
  opacity: 1;
  cursor: pointer;
}
.filters .drop-down-filters .lists .list select {
  margin: 0 10px 10px 10px;
  scrollbar-width: none;
  -ms-overflow-style: none;
  min-width: 150px;
}
.filters .drop-down-filters .lists .list select option {
  margin: 1px 0;
  padding: 2px 5px;
}
.filters .drop-down-filters .lists .list select option:hover {
  background: rgba(254, 115, 32, 0.2196078431);
}
.filters .drop-down-filters .lists .list select option:checked {
  background: #fe7320;
  color: white;
}
.filters .drop-down-filters .lists .list select[multiple]:focus option:checked {
  background: #fe7320 linear-gradient(0deg, #fe7320 0%, #fe7320 100%);
}
.filters .drop-down-filters .lists .list select::-webkit-scrollbar {
  display: none;
}
.filters .drop-down-filters .lists .list .clear-list {
  font-size: 11px;
  color: #37474F;
  opacity: 0.5;
  cursor: pointer;
}
.filters .drop-down-filters .lists .list .clear-list:hover {
  color: #fe7320;
  opacity: 1;
}
.filters .drop-down-btn {
  margin: 0 auto;
  z-index: 1000;
  width: fit-content;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 11px;
  font-weight: 500;
  opacity: 0;
  transition: opacity 2s;
}
.filters .drop-down-btn.active {
  opacity: 1;
}
.filters .drop-down-btn img {
  transition: all 0.8s;
  width: 40px;
  cursor: pointer;
}
.filters .drop-down-btn img.up {
  transform: rotate(-180deg);
}
.filters .drop-down-btn .more-filters {
  margin-bottom: -10px;
  overflow: hidden;
  max-height: 16px;
  opacity: 1;
  transition: all 0.8s;
  color: #fe7320;
}
.filters .drop-down-btn .more-filters.hide {
  max-height: 0;
  opacity: 0;
}
.filters .drop-down-btn .less-filters {
  margin-top: -10px;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: all 0.8s;
  color: #fe7320;
}
.filters .drop-down-btn .less-filters.show {
  max-height: 16px;
  opacity: 1;
}

.slider-values {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: -15px;
}

.slider-container {
  display: flex;
  width: 100%;
}
.slider-container .container {
  width: 100%;
  position: relative;
  height: 50px;
}
.slider-container .container .slider-track {
  background: #fe7320;
  width: 100%;
  height: 2px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 5px;
}
.slider-container .container input[type=range] {
  appearance: none;
  width: 100%;
  outline: none;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background-color: transparent;
  pointer-events: none;
}
.slider-container .container input[type=range]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 5px;
}
.slider-container .container input[type=range]::-moz-range-track {
  -moz-appearance: none;
  height: 5px;
}
.slider-container .container input[type=range]::-ms-track {
  appearance: none;
  height: 5px;
}
.slider-container .container input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  background-color: #fe7320;
  border: solid 2px #fe7320;
  cursor: pointer;
  border-radius: 50%;
  pointer-events: auto;
}
.slider-container .container input[type=range]::-moz-range-thumb {
  -moz-appearance: none;
  height: 15px;
  width: 15px;
  outline: none;
  background-color: #fe7320;
  border: solid 2px #fe7320;
  cursor: pointer;
  border-radius: 50%;
  pointer-events: auto;
}
.slider-container .container input[type=range]::-ms-thumb {
  appearance: none;
  height: 15px;
  width: 15px;
  background-color: #fe7320;
  border: solid 2px #fe7320;
  cursor: pointer;
  border-radius: 50%;
  pointer-events: auto;
}
.slider-container .container input[type=range]:active::-webkit-slider-thumb {
  background-color: #FFFFFF;
  border: solid 2px #fe7320;
}
.slider-container .container input[type=range]:active::-moz-range-thumb {
  background-color: #FFFFFF;
  border: solid 2px #fe7320;
}
.slider-container .container input[type=range]:active::-ms-thumb {
  background-color: #FFFFFF;
  border: solid 2px #fe7320;
}
.slider-container .container input[type=range]:hover::-webkit-slider-thumb {
  outline: 10px solid rgba(254, 115, 32, 0.2196078431);
}
.slider-container .container input[type=range]:hover::-moz-range-thumb {
  outline: 10px solid rgba(254, 115, 32, 0.2196078431);
}
.slider-container .container input[type=range]:hover::-ms-thumb {
  outline: 10px solid rgba(254, 115, 32, 0.2196078431);
}

.results {
  margin: 25px 25px 30px 25px;
}

.awarded {
  margin: 25px 25px 50px 25px;
}
.awarded .result .top .branche {
  margin-left: 10px;
}
.awarded .result .ribbon-wrap {
  width: 75px;
  height: 75px;
  overflow: hidden;
  position: absolute;
  border-radius: 2px;
  top: -8px;
  left: -8px;
}
.awarded .result .ribbon-wrap .ribbon {
  width: 125px;
  font-size: 5px;
  text-align: center;
  padding: 2px 0;
  background: #ff2166;
  color: #fff;
  position: absolute;
  transform: rotate(-45deg);
  left: -31px;
  top: 23px;
}
.awarded .result .ribbon-wrap .ribbon img {
  height: 11px;
}
.awarded .result .ribbon-wrap::before, .awarded .result .ribbon-wrap::after {
  content: "";
  width: 10px;
  height: 10px;
  background: #bb1c4e;
  position: absolute;
  z-index: -1;
}
.awarded .result .ribbon-wrap::before {
  top: 0;
  right: 0;
}
.awarded .result .ribbon-wrap::after {
  bottom: 0;
  left: 0;
}

